.header {
  background: #FFFFFF;
  box-shadow: 0 3px 6px #00000029;
  position: fixed;
  width: 100%;
  z-index: 3;
}

.header .container {
  max-width: 95%;
  padding: 0 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  height: 70px;
}

.header__logo {
  font-family: "Raleway", sans-serif;
  font-size: 16px*2;
  font-weight: 300;
  color: #08A6E4;
  text-align: left;
}

.header .hamburger {
  padding: 10px;
  margin-right: 20px;
  display: none;
  cursor: pointer;
  background: transparent;
  border: 0;
  margin: 0;
  outline: 0;
}

.header .hamburger__box {
  width: 40px;
  height: 12px;
  display: inline-block;
  position: relative;
}

.header .hamburger__inner {
  width: 100%;
  height: 3px;
  position: absolute;
  background-color: #08A6E4;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  transition: background-color .1s .2s ease-in-out;
}

.header .hamburger__inner::before, .header .hamburger__inner::after {
  width: 100%;
  height: 3px;
  position: absolute;
  background-color: #08A6E4;
  content: "";
  left: 0;
  transition: transform .2s .2s ease-in-out;
}

.header .hamburger__inner::before {
  top: -10px;
}

.header .hamburger__inner::after {
  top: 10px;
}

.header .hamburger--active .hamburger__inner {
  background-color: transparent;
}

.header .hamburger--active .hamburger__inner::before {
  transform: translateY(10px) rotate(45deg);
}

.header .hamburger--active .hamburger__inner::after {
  transform: translateY(-10px) rotate(-45deg);
}

.header .nav__list {
  display: flex;
}

.header .nav__item {
  margin-left: 94px;
}

.header .nav__item:first-child {
  margin-left: 0;
}

.header .nav__link {
  font: "Open Sans", sans-serif;
  font-size: 16px/1.454;
  line-height: 70px;
  font-weight: 600;
  letter-spacing: 3.28px;
  color: #A5A5A5;
  text-transform: uppercase;
  display: block;
  position: relative;
}

.header .nav__link:hover, .header .nav__link.active, .header .nav__link.active:hover {
  color: #000000;
}

.header .nav__link:hover::before, .header .nav__link.active::before, .header .nav__link.active:hover::before {
  width: calc(100% + 10px);
  left: -5px;
}

.header .nav__link::before {
  content: '';
  display: block;
  position: absolute;
  background: #08A6E4;
  height: 4px;
  width: 0;
  bottom: 0;
  left: 50%;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

@media (max-width: 880px) {
  .header .container {
    max-width: 100%;
  }
  .header__logo {
    margin-left: 20px;
  }
  .header .nav .hamburger {
    display: inline-block;
  }
  .header .nav__list {
    display: none;
    width: 100%;
  }
  .header .nav__list--active {
    width: 100%;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 71px;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #ffffff;
    box-shadow: 0 3px 6px #00000029;
    transition: all .2s .2s ease;
  }
  .header .nav__item {
    width: 100%;
    text-align: center;
    margin: 0;
    padding: 0;
  }
  .header .nav__link {
    font-size: 11.2px;
    width: 100%;
    line-height: 60px;
  }
  .header .nav__link:hover::before, .header .nav__link.active::before, .header .nav__link.active:hover::before {
    width: 0;
  }
}

