  border: none;
  font: 700 13px/18px Open Sans;
  background: #08A6E4;
  box-shadow: 5px 10px 14px #07ACE629;
  border-radius: 35px;
  position: absolute;
  bottom: 0;
  right: 0;
  cursor: pointer;
  outline: none;
}

.contact .form__btn:hover {
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  transition: all 0.5s ease;
  transform: scale(1.05);
  background-color: #0794cb;
  box-shadow: 5px 10px 14px rgba(2, 61, 82, 0.5);
}

.contact__left {
  width: 49%;
}

.contact__title {
  padding-bottom: 22px;
  margin-top: 0;
}

.contact__text {
  font-size: 16px;
  padding-bottom: 42px;
}

.contact__data {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.contact__email, .contact__phone {
  font-size: 16px;
  font-weight: 600;
  color: #A5A5A5;
  margin-right: 24px;
  margin-left: 37px;
  position: relative;
}

.contact__email::before {
  content: "";
  display: block;
  position: absolute;
  background: transparent url("../assets/Mail Icon.svg") 0% 0% no-repeat padding-box;
  width: 27px;
  height: 27px;
  top: -5px;
  left: -35px;
}

.contact__phone::before {
  content: "";
  display: block;
  position: absolute;
  background: transparent url("../assets/Phone Icon.svg") 0% 0% no-repeat padding-box;
  width: 27px;
  height: 27px;
  top: -5px;
  left: -37px;
}

@media (max-width: 1000px) {
  .contact {
    padding: 50px 0 60px 0;
  }
  .contact .content {
    flex-direction: column;
    align-items: center;
    height: auto;
  }
  .contact__left {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 50px;
  }
  .contact__title {
    font-size: 40px;
    padding-bottom: 12px;
  }
  .contact__text {
    font-size: 16px;
    padding-bottom: 32px;
  }
  .contact__data {
    flex-direction: column;
    justify-content: center;
  }
  .contact__email, .contact__phone {
    display: block;
    padding: 10px 0;
  }
  .contact__email::before {
    top: 10px;
    left: -35px;
  }
  .contact__phone::before {
    top: 10px;
    left: -37px;
  }
  .contact__right {
    width: 100%;
    padding: 0 50px;
    margin-top: 50px;
  }
  .contact .form__name, .contact .form__email {
    color: #08A6E4;
    display: block;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 3.28px;
    line-height: 1.5em;
    text-transform: uppercase;
  }
  .contact .form__email {
    margin-top: 42px;
  }
  .contact .form__btn {
    margin-top: 20px;
    position: relative;
  }
}

.calculator {
  padding: 40px 50px;
  background: #F7F7F7;
  height: 70vh;
}

.calculator__title {
  text-align: center;
  margin: 40px;
}

.calculator .container {
  display: flex;
  justify-content: space-between;
  max-width: 1000px;
  height: 100%;
}

.calculator__data {
  width: 440px;
  max-width: 100%;
}

.calculator__form {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.calculator__form label[for=checkbox__accounting],
.calculator__form label[for=checkbox__rental] {
  padding-left: 34px;
  display: block;
  cursor: pointer;
  position: relative;
}

.calculator__form label[for=checkbox__accounting] p,
.calculator__form label[for=checkbox__rental] p {
  display: inline-block;
  color: #707070;
  font-size: 12px;
  text-align: left;
  margin-top: 20px;
  height: 24px;
  line-height: 24px;
}

.calculator__form label[for=checkbox__accounting]::before,
.calculator__form label[for=checkbox__rental]::before {
  position: absolute;
  left: 0;
  top: 0;
  content: '';
  display: block;
  background: #FFFFFF;
  border: 1px solid #95989A;
  border-radius: 4px;
  opacity: 1;
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  margin-top: 20px;
}

.calculator__form #checkbox__accounting:checked ~ label[for=checkbox__accounting]::before {
  background: #FFFFFF url("../assets/Checkmark.svg") no-repeat center center;
}

.calculator__form #checkbox__rental:checked ~ label[for=checkbox__rental]::before {
  background: #FFFFFF url("../assets/Checkmark.svg") no-repeat center center;
}

.calculator__form #checkbox__accounting,
.calculator__form #checkbox__rental {
  display: none;
}

.calculator__products-input, .calculator__orders-input {
  display: inline-block;
  width: 100%;
  height: 40px;
  background: #FFFFFF;
  box-shadow: 0px 3px 6px #08A6E429;
  border: 1px solid #08A6E4;
  border-radius: 6px;
  outline: none;
  color: #707070;
  padding: 0 15px;
}

.calculator__products-input::placeholder, .calculator__orders-input::placeholder {
  text-align: left;
  font-size: 13px;
  color: #A5A5A5;
}

.calculator__orders-input {
  margin-top: 20px;
}

.calculator__package {
  width: 100%;
}

.calculator__package-select {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 40px;
  margin-top: 20px;
  background: #FFFFFF;
  box-shadow: 0px 3px 6px #08A6E429;
  border: 1px solid #08A6E4;
  border-radius: 6px;
  text-align: left;
  padding: 0 15px;
}

.calculator__package-text {
  font-size: 13px;
  line-height: 40px;
  color: #707070;
}

.calculator__package-list {
  list-style: none;
  background: #FFFFFF;
  box-shadow: 0px 3px 6px #EB476E29;
  border: 1px solid #EB476E;
  border-radius: 6px;
  margin-top: 10px;
  padding: 10px 0px;
}

.calculator__package-item {
  font-size: 13px;
  padding: 0 20px;
}

.calculator__package-item:hover {
  background: #F7F7F7;
  box-shadow: 0px 3px 6px #07ACE629;
  border-radius: 6px;
}

.calculator__package-item:nth-child(2) {
  margin: 15px 0;
}

.calculator__result {
  display: flex;
  width: 385px;
  max-width: 100%;
  flex-direction: column;
  align-items: flex-end;
}

.calculator__result-products, .calculator__result-orders, .calculator__result-package, .calculator__result-accounting, .calculator__result-terminal, .calculator__result-total {
  height: 31px;
  width: 100%;
  background: #55DFB4;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 6px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  color: #FFFFFF;
  font-size: 14px;
  align-items: center;
  padding: 0 20px;
}

.calculator__result-total {
  width: 180px;
  height: 38px;
  background: #08A6E4;
  margin-top: 40px;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

.is-hidden {
  display: none;
}

.up {
  transform: rotate(180deg);
}

@media (max-width: 1000px) {
  .calculator {
    height: auto;
  }
  .calculator .container {
    flex-direction: column;
    align-items: center;
  }
  .calculator__form {
    align-items: flex-start;
  }
  .calculator__result {
    margin-top: 50px;
    align-items: flex-start;
  }
  .calculator__result-total {
    margin-top: 20px;
    align-self: flex-end;
  }
}

.footer {
  height: 89px;
  background-color: #F7F7F7;
  box-shadow: 0 -5px 40px #00000029;
}

.footer .container {
  height: 100%;
  padding: 0 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__logo-link {
  font: 300 26px Raleway;
  color: #08A6E4;
}

.footer__text {
  font-size: 11px;
  color: #A5A5A5;
}

.footer__twitter {
  display: inline-block;
  width: 24px;
  height: 24px;
  background: url("../assets/Twitter.svg");
  margin-right: 15px;
}

.footer__facebook {
  display: inline-block;
  width: 24px;
  height: 24px;
  background: url("../assets/Facebook.svg");
}
